export const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000/api';

export const API_ENDPOINTS = {
  // Auth
  LOGIN: '/auth/login',
  REGISTER: '/auth/register',
  VERIFY_TOKEN: '/auth/verify',

  // Users
  USERS: '/users',
  USER_PROFILE: (id: string) => `/users/${id}`,
  USER_TASKS: (id: string) => `/users/${id}/tasks`,
  USER_STATS: (id: string) => `/users/${id}/stats`,

  // Tasks
  TASKS: '/tasks',
  TASK_DETAIL: (id: string) => `/tasks/${id}`,
  TASK_STATUS: (id: string) => `/tasks/${id}/status`,

  // Projects
  PROJECTS: '/projects',
  PROJECT_DETAIL: (id: string) => `/projects/${id}`,
  PROJECT_MEMBERS: (id: string) => `/projects/${id}/members`,

  // Analytics
  DASHBOARD_STATS: '/analytics/dashboard',
  PERFORMANCE_STATS: '/analytics/performance',

  // Notifications
  NOTIFICATIONS: '/notifications',
  MARK_NOTIFICATION_READ: (id: string) => `/notifications/${id}/read`,
};

export const DEFAULT_HEADERS = {
  'Content-Type': 'application/json',
};

export const getAuthHeader = () => {
  const token = localStorage.getItem('auth_token');
  return token ? { Authorization: `Bearer ${token}` } : {};
};

export const handleApiError = (error: any) => {
  if (error.response) {
    // Server responded with a status code outside the 2xx range
    throw new Error(error.response.data.error || 'An error occurred');
  } else if (error.request) {
    // Request was made but no response received
    throw new Error('No response from server');
  } else {
    // Error setting up the request
    throw new Error('Error setting up request');
  }
};
