import { 
  ApiResponse, 
  LoginResponse, 
  User, 
  Task, 
  Project, 
  DashboardStats, 
  LoginFormData,
  RegisterFormData,
  TaskFormData,
  ProjectFormData,
  Notification
} from '@/types';
import { API_BASE_URL, API_ENDPOINTS, DEFAULT_HEADERS, getAuthHeader, handleApiError } from '@/config/api';

class ApiService {
  private async request<T>(
    endpoint: string,
    options: RequestInit = {}
  ): Promise<ApiResponse<T>> {
    try {
      const authHeader = getAuthHeader();
      const headers = new Headers({
        ...DEFAULT_HEADERS,
        ...(authHeader as Record<string, string>),
        ...(options.headers as Record<string, string>),
      });

      const response = await fetch(`${API_BASE_URL}${endpoint}`, {
        ...options,
        headers,
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || 'An error occurred');
      }

      return data;
    } catch (error) {
      handleApiError(error);
      throw error;
    }
  }

  // Auth endpoints
  async login(credentials: LoginFormData): Promise<LoginResponse> {
    const response = await this.request<LoginResponse>(API_ENDPOINTS.LOGIN, {
      method: 'POST',
      body: JSON.stringify(credentials),
    });
    return response.data!;
  }

  async register(userData: RegisterFormData): Promise<User> {
    const response = await this.request<User>(API_ENDPOINTS.REGISTER, {
      method: 'POST',
      body: JSON.stringify(userData),
    });
    return response.data!;
  }

  async verifyToken(): Promise<User> {
    const response = await this.request<User>(API_ENDPOINTS.VERIFY_TOKEN);
    return response.data!;
  }

  // User endpoints
  async getUserProfile(userId: string): Promise<User> {
    const response = await this.request<User>(API_ENDPOINTS.USER_PROFILE(userId));
    return response.data!;
  }

  async updateUserProfile(userId: string, data: Partial<User>): Promise<User> {
    const response = await this.request<User>(API_ENDPOINTS.USER_PROFILE(userId), {
      method: 'PUT',
      body: JSON.stringify(data),
    });
    return response.data!;
  }

  // Task endpoints
  async getTasks(): Promise<Task[]> {
    const response = await this.request<Task[]>(API_ENDPOINTS.TASKS);
    return response.data!;
  }

  async createTask(taskData: TaskFormData): Promise<Task> {
    const response = await this.request<Task>(API_ENDPOINTS.TASKS, {
      method: 'POST',
      body: JSON.stringify(taskData),
    });
    return response.data!;
  }

  async updateTaskStatus(taskId: string, status: Task['status']): Promise<Task> {
    const response = await this.request<Task>(API_ENDPOINTS.TASK_STATUS(taskId), {
      method: 'PUT',
      body: JSON.stringify({ status }),
    });
    return response.data!;
  }

  // Project endpoints
  async getProjects(): Promise<Project[]> {
    const response = await this.request<Project[]>(API_ENDPOINTS.PROJECTS);
    return response.data!;
  }

  async createProject(projectData: ProjectFormData): Promise<Project> {
    const response = await this.request<Project>(API_ENDPOINTS.PROJECTS, {
      method: 'POST',
      body: JSON.stringify(projectData),
    });
    return response.data!;
  }

  async updateProject(projectId: string, data: Partial<Project>): Promise<Project> {
    const response = await this.request<Project>(API_ENDPOINTS.PROJECT_DETAIL(projectId), {
      method: 'PUT',
      body: JSON.stringify(data),
    });
    return response.data!;
  }

  // Analytics endpoints
  async getDashboardStats(): Promise<DashboardStats> {
    const response = await this.request<DashboardStats>(API_ENDPOINTS.DASHBOARD_STATS);
    return response.data!;
  }

  // Notification endpoints
  async getNotifications(): Promise<Notification[]> {
    const response = await this.request<Notification[]>(API_ENDPOINTS.NOTIFICATIONS);
    return response.data!;
  }

  async markNotificationAsRead(notificationId: string): Promise<void> {
    await this.request(API_ENDPOINTS.MARK_NOTIFICATION_READ(notificationId), {
      method: 'PUT',
    });
  }

  // Admin endpoints
  async getAllUsers(): Promise<User[]> {
    const response = await this.request<User[]>(API_ENDPOINTS.USERS);
    return response.data!;
  }

  async createUser(userData: RegisterFormData): Promise<User> {
    const response = await this.request<User>(API_ENDPOINTS.USERS, {
      method: 'POST',
      body: JSON.stringify(userData),
    });
    return response.data!;
  }

  async deleteUser(userId: string): Promise<void> {
    await this.request(API_ENDPOINTS.USER_PROFILE(userId), {
      method: 'DELETE',
    });
  }
}

// Create a singleton instance
const apiService = new ApiService();

export default apiService;
